@font-face {
  font-family: Manrope;
  src: url("Manrope-Regular.f570be85.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Semibold.c57db7c0.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Semibold.c57db7c0.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: Bebas Neue;
  src: url("BebasNeueRegular.f74a33b5.woff") format("woff");
