}

.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  opacity: 1;
  box-sizing: border-box;
  background-color: #f8f8f8;
  border-radius: 0;
  width: 7px;
  height: 7px;
  margin: 0;
  transform: rotate(45deg);
}

.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #00000082;
  border: 1px solid #fff;
}

.page {
  scroll-behavior: smooth;
  height: 100vh;
  margin: 0;
  font-family: Manrope, sans-serif;
  position: relative;
}

.page:after {
  content: "";
  z-index: -2;
  background-image: url("background-header.4d6cdb23.png");
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width <= 640px) {
  .page:after {
    display: none;
  }
}

.page__body {
  margin: 0;
}

.page__menu {
  pointer-events: none;
  opacity: 0;
  z-index: 1;
  background-color: #03839e;
  width: 50%;
  height: 100vh;
  transition: all .3s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  pointer-events: auto;
  opacity: 1;
  overflow: auto;
  transform: translateX(0);
}

@media (width <= 1280px) {
  .page__menu {
    display: block;
  }
}

@media (width <= 640px) {
  .page__menu {
    width: 70%;
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

p {
  margin: 0;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

@media (width <= 1280px) {
  .container {
    max-width: 768px;
    padding-inline: 34px;
  }
}

@media (width <= 1280px) and (width <= 640px) {
  .container {
    padding-inline: 30px;
  }
}

.title {
  letter-spacing: 5.58px;
  text-transform: uppercase;
  margin: 0 0 34px;
  padding: 0;
  font-family: Bebas Neue, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 150%;
}

@media (width <= 1280px) {
  .title {
    margin-bottom: 10px;
  }
}

@media (width <= 640px) {
  .title {
    margin-bottom: 25px;
  }
}

.title-block {
  letter-spacing: 8.99px;
  margin: 0;
  font-family: Bebas Neue, sans-serif;
  font-size: 58px;
  font-weight: 400;
}

.text {
  color: #4f4f4f;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.vertical-text {
  letter-spacing: 2.64px;
  color: #828282;
  font-family: Bebas Neue, sans-serif;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
}

.vertical-text:before {
  content: "";
  background-color: #828282;
  width: 65px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-100%);
}

.vertical-text--top {
  transform-origin: 100% 0;
  padding-left: 75px;
  transform: rotate(90deg);
}

.vertical-text--bottom {
  transform-origin: 0 100%;
  padding-left: 75px;
  transform: rotate(-90deg);
}

.button {
  text-transform: uppercase;
  color: #121212;
  cursor: pointer;
  text-align: center;
  background-color: #ffe339;
  border: none;
  width: 270px;
  height: 48px;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 48px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: block;
  position: relative;
}

.button:after {
  content: "";
  background-image: url("arrow-right.0e6613e3.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 28px;
  height: 8px;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: 50%;
  left: 70%;
  transform: translateY(50%);
}

.button:hover {
  opacity: .8;
}

.button:hover:after {
  transform: translate(5px, 50%);
}

.hover-link {
  width: -moz-fit-content;
  width: fit-content;
  transition: all .3s ease-in-out;
  display: block;
  position: relative;
  overflow-x: hidden;
}

.hover-link:hover {
  color: #03839e;
}

.hover-link:after {
  content: "";
  border-bottom: 1px solid #03839e;
  width: 100%;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateX(-100%);
}

.hover-link:hover:after {
  transform: translateX(0);
}

